<template>
  <van-swipe
    :autoplay="5000"
    indicator-color="#0055ff"
  >
    <van-swipe-item
      v-for="(image, index) in images"
      :key="index"
    >
      <img v-lazy="image" />
    </van-swipe-item>
  </van-swipe>
</template>

<script>
export default {
  data() {
    return {
      images: ["/static/image/banner.png", "/static/image/banner.png"]
    };
  }
};
</script>

<style lang="less" scoped>
.van-swipe {
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.08);
  -webkit-box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.08);
  -moz-box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.08);
  .van-swipe__track {
    .van-swipe-item {
      img {
        width: 10rem;
        height: 4rem;
      }
    }
  }
}
</style>




