<template>
  <div class="page">
    <!-- 搜索与消息 -->
    <search/>
    <!-- 轮播图 -->
    <banner/>
    <!-- 商品分类 -->
    <categorie/>
    <!-- 底部导航栏 -->
    <tabBar :Tabbar="0" />
  </div>
</template>

<script>
import search from '../components/Search'
import banner from '../components/home/Banner'
import categorie from '../components/home/Categorie'
import tabBar from '../components/TabBar'
export default {
  data() {
    return {
    };
  },
  components: {
    search,
    banner,
    categorie,
    tabBar,
  }
};
</script>

<style>
.van-icon-search:before {
  font-weight: bold;
}
.van-search__action {
  padding: 0 18px;
}
.van-swipe__track .van-swipe-item img {
  width: 100%;
  height: 100%;
}
.van-tabbar .van-tabbar-item {
  color: #000000;
}
</style>
<style lang="less" scoped>
.page {
  min-height: 100%;
  background: #f1f1f1;
}
</style>


