<template>
  <div class="BulletBox">
    <div class="group">
      <div class="titleGroup">
        <div>
          <h6>背包24-2</h6>
          <h5>￥320/个</h5>
        </div>
        <van-icon
          name="close"
          size=".8rem"
          color="#8f8f8f"
          @click="onClose"
        />
      </div>
      <div class="normGroup">
        <h6>包装规格(1件20个）</h6>
        <div>
          <van-radio
            v-model="normChecked"
            @click="onNorm(1)"
          >
            <div
              slot="icon"
              :class="normChecked!=1 ? 'radio' : 'norm'"
            >
              个
            </div>
          </van-radio>
          <van-radio
            v-model="normChecked"
            @click="onNorm(2)"
          >
            <div
              slot="icon"
              :class="normChecked!=2 ? 'radio' : 'norm'"
            >
              件
            </div>
          </van-radio>
        </div>
      </div>
      <div class="colourGroup">
        <h6>颜色</h6>
        <div>
          <van-radio
            v-model="colourChecked"
            @click="onColour(0)"
          >
            <div
              slot="icon"
              class="colour"
              :style="{backgroundColor: 'blue'}"
            >
              <div
                class="mask"
                v-show="colourChecked == 0? false: true"
              ></div>
            </div>
          </van-radio>
          <van-radio
            v-model="colourChecked"
            @click="onColour(1)"
          >
            <div
              slot="icon"
              class="colour"
              :style="{backgroundColor: 'black'}"
            >
              <div
                class="mask"
                v-show="colourChecked == 1? false: true"
              ></div>
            </div>
          </van-radio>
          <van-radio
            v-model="colourChecked"
            @click="onColour(2)"
          >
            <div
              slot="icon"
              class="colour"
              :style="{backgroundColor: 'red'}"
            >
              <div
                class="mask"
                v-show="colourChecked == 2? false: true"
              ></div>
            </div>
          </van-radio>
          <van-radio
            v-model="colourChecked"
            @click="onColour(3)"
          >
            <div
              slot="icon"
              class="colour"
              :style="{backgroundColor: 'yellow'}"
            >
              <div
                class="mask"
                v-show="colourChecked == 3? false: true"
              ></div>
            </div>
          </van-radio>
        </div>
      </div>
      <div class="buyGroup">
        <div>购买数量（库存：5件30个）</div>
        <van-stepper v-model="value" />
      </div>
      <div class="remarkGroup">
        <van-cell-group>
          <van-field
            v-model="message"
            type="textarea"
            placeholder="点击录入备注"
            rows="1"
            autosize
          />
        </van-cell-group>
      </div>
      <div class="confirmGroup">
        <div class="subtotal">小计：¥0.00</div>
        <div class="confirm">确认</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false, //关闭
      normChecked: "1", // 规格选择
      colourChecked: "0", //颜色选择
      value: 1, // 商品数量
      message: '', //备注
    };
  },
  created() {},
  methods: {
    // 关闭购买
    onClose() {
      this.$emit('onClose', this.show)
    },
    // 规格选择
    onNorm(checked) {
      this.normChecked = checked;
    },
    //颜色选择
    onColour(checked) {
      this.colourChecked = checked;
    },
  }
};
</script>

<style>
.van-stepper__minus,
.van-stepper__plus {
  width: 0.8rem;
  height: 0.53rem;
}
.van-stepper__input {
  width: 0.96rem;
  height: 0.53rem;
}
.van-hairline--top-bottom::after {
  border: none;
}
.remarkGroup .van-cell-group .van-cell {
  padding: 0 15px;
  color: #0055ff;
  font-size: 12px;
}
.remarkGroup .van-cell-group .van-cell .van-cell__value--alone .van-field__body textarea::-webkit-input-placeholder{
  color: #0055ff;
}
.remarkGroup .van-cell-group .van-cell .van-cell__value--alone .van-field__body textarea:-moz-placeholder {
  color: #0055ff;
}
.remarkGroup .van-cell-group .van-cell .van-cell__value--alone .van-field__body textarea::-moz-placeholder { 
  color: #0055ff;
}
.remarkGroup .van-cell-group .van-cell .van-cell__value--alone .van-field__body textarea:-ms-input-placeholder { 
  color: #0055ff;
}
</style>

<style lang="less" scoped>
.BulletBox {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  .group {
    position: absolute;
    left: 0;
    bottom: 1.68rem;
    width: 10rem;
    background-color: #ffffff;
    .titleGroup {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      height: 1.48rem;
      margin: 0.35rem 0.27rem 0;
      border-bottom: solid 1px #e5e5e5;
      h6 {
        font-size: 15px;
        line-height: 0.4rem;
      }
      h5 {
        margin-top: 0.4rem;
        font-size: 15px;
        line-height: 0.4rem;
        font-weight: 300;
      }
    }
    .normGroup {
      margin: 0.35rem 0.27rem 0;
      border-bottom: solid 1px #e5e5e5;
      h6 {
        margin-bottom: 0.32rem;
        font-size: 12px;
        line-height: 0.32rem;
      }
      div {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        flex-direction: row;
        margin-bottom: 0.24rem;
        .van-radio {
          height: 0.67rem;
          .radio {
            display: inline-block;
            width: 1.6rem;
            height: 0.67rem;
            margin-right: 0.6rem;
            background-color: #e5e5e5;
            border-radius: 0.33rem;
            line-height: 0.67rem;
            text-align: center;
          }
          .norm {
            display: inline-block;
            width: 1.6rem;
            height: 0.67rem;
            margin-right: 0.6rem;
            border-radius: 0.33rem;
            background-color: #0055ff;
            color: #ffffff;
            line-height: 0.67rem;
            text-align: center;
          }
        }
      }
    }
    .colourGroup {
      margin: 0.2rem 0.27rem 0;
      border-bottom: solid 1px #e5e5e5;
      h6 {
        margin-bottom: 0.28rem;
        font-size: 12px;
        line-height: 0.32rem;
      }
      div {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        flex-direction: row;
        margin-bottom: 0.24rem;
        .van-radio {
          height: 0.8rem;
          .colour {
            position: relative;
            width: 0.8rem;
            height: 0.8rem;
            margin-right: 0.53rem;
            border-radius: 50%;
            .mask {
              position: absolute;
              top: 0;
              left: 0;
              width: 0.8rem;
              height: 0.8rem;
              border-radius: 50%;
              background-color: rgba(255, 255, 255, 0.5);
            }
          }
        }
      }
    }
    .buyGroup {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 0.24rem;
      margin: 0.13rem 0.27rem 0;
      border-bottom: solid 1px #e5e5e5;
      font-size: 12px;
      line-height: 0.32rem;
    }
    .remarkGroup {
      margin: 0.2rem 0.27rem 0;
      border-bottom: solid 1px #e5e5e5;
    }
    .confirmGroup {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      height: 1.04rem;
      margin-left: .27rem;
      font-size: 15px;
      .subtotal {
        color: #ff0000;
      }
      .confirm {
        width: 2.37rem;
        height: 1.07rem;
        background-color: #0055ff;
        color: #ffffff;
        line-height: 1.07rem;
        text-align: center;
      }
    }
  }
}
</style>



