<template>
  <li>
    <div class="goodImg">
      <img
        src="/static/image/banner.png"
        alt=""
      >
    </div>
    <h6>背包24-2;背包24-2;背包24-2;背包24-2;</h6>
    <p>包装规格：1件*20个</p>
    <h5>￥320.00</h5>
    <van-icon name="add" size=".53rem" color="#0055ff" @click="onCart" />
    <bulletBox v-show="show" @onClose="onClose" />
  </li>
</template>

<script>
import bulletBox from './BulletBox'
export default {
  data () {
    return {
      show: false
    }
  },
  created () {
    
  },
  methods: {
    onCart() {
      this.show = true
    },
    onClose() {
      this.show = false
    }
  },
  components: {
    bulletBox
  }
};
</script>

<style lang="less" scoped>
li {
  position: relative;
  width: 4.6rem;
  height: 5.47rem;
  margin-top: 10px;
  background-color: #ffffff;
  border-radius: 6px;
  .goodImg {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 4.6rem;
    height: 3.6rem;
    border-radius: 6px 6px 0 0;
    background: #e8e8e8;
    img {
      max-width: 4.52rem;
      max-height: 2.8rem;
    }
  }
  h6 {
    padding: 0 0.16rem;
    margin-top: 0.28rem;
    font-size: 12px;
    line-height: 0.32rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  p {
    padding: 0 0.16rem;
    margin-top: 0.11rem;
    color: #c2c2c2;
    font-size: 9px;
    line-height: 0.24rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  h5 {
    padding: 0 0.16rem;
    margin-top: 0.31rem;
    color: #000000;
    font-size: 13px;
    line-height: 0.35rem;
    font-weight: 300;
  }
  .van-icon {
    position: absolute;
    right: 0.27rem;
    bottom: 0.23rem;
  }
}
</style>




