<template>
  <form action="/">
    <van-search
      v-model="value"
      shape="round"
      placeholder="请输入搜索关键词"
      :showAction="true"
    >
      <div
        slot="action"
        class="message"
      >
        <span>5</span>
      </div>
    </van-search>
  </form>
</template>

<script>
export default {
  data() {
    return {
      value: ""
    };
  }
};
</script>

<style lang="less" scoped>
.van-search {
  .message {
    position: relative;
    width: .47rem;
    height: .41rem;
    background-image: url("/static/image/message.png");
    background-repeat: no-repeat;
    background-size: cover;
    span {
      position: absolute;
      top: -.23rem;
      right: -.2rem;
      width: .35rem;
      height: .35rem;
      border-radius: 50%;
      background-color: #ff0000;
      color: #ffffff;
      font-size: 9px;
      line-height: .35rem;
      text-align: center;
    }
  }
}
</style>




