<template>
  <div class="group">
    <ul class="categories">
      <li class="activeSort">全部</li>
      <li>常购</li>
      <li>新品</li>
      <li>促销</li>
    </ul>
    <ol>
      <good
        v-for="(item,index) in 4"
        :key="index"
      />
    </ol>
  </div>
</template>

<script>
import good from "../good/Good";
export default {
  components: {
    good
  }
};
</script>

<style lang="less" scoped>
.group {
  padding: 0.35rem 0.27rem;
  margin-bottom: 50px;
  .categories {
    display: inline-block;
    li {
      float: left;
      width: 2.28rem;
      height: 0.89rem;
      border-radius: 5px 5px 0px 0px;
      background-color: #ffffff;
      color: #424d64;
      line-height: 0.89rem;
      text-align: center;
    }
    li.activeSort {
      width: 2.62rem;
      background: #0055ff;
      color: #ffffff;
    }
  }
  ol {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    flex-flow: wrap;
    justify-content: space-between;
  }
}
</style>




